<!--  -->
<template>
  <div class="">
    <el-form
      :inline="true"
      :model="dataForm"
      @keyup.enter.native="getDataList()"
    >
      <el-form-item>
        <el-button @click="changeTimeOrder()" type="success"
          >时间{{ timeOrder ? "升序" : "降序" }}</el-button
        >

        <el-button
          type="danger"
          @click="deleteHandle()"
          :disabled="dataListSelections.length <= 0"
          >批量删除</el-button
        >
      </el-form-item>
    </el-form>
    <div></div>
    <el-table
      :data="dataList"
      border
      v-loading="dataListLoading"
      @selection-change="selectionChangeHandle"
      style="width: 100%"
    >
      <el-table-column
        type="selection"
        header-align="center"
        align="center"
        width="50"
      >
      </el-table-column>
      <el-table-column
        prop="userName"
        header-align="center"
        align="center"
        label="用户名"
      >
      </el-table-column>
      <el-table-column
        prop="ip"
        header-align="center"
        align="center"
        label="ip来源"
      >
      </el-table-column>
      <el-table-column
        prop="businessName"
        header-align="center"
        align="center"
        label="登录/退出"
      >
      </el-table-column>

      <el-table-column
        prop="nowTime"
        header-align="center"
        align="center"
        label="访问时间"
      >
      </el-table-column>
      <el-table-column
        prop="role"
        header-align="center"
        align="center"
        label="身份"
      >
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChangeHandle"
      @current-change="currentChangeHandle"
      :current-page="pageIndex"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="pageSize"
      :total="totalPage"
      layout="total, sizes, prev, pager, next, jumper"
    >
    </el-pagination>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      uploadHeaders: { token: this.$store.state.user.token },
      token: "",
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataList: [],
      dataForm: {
        key: "",
      },
      timeOrder: false,
      dataListSelections: [],
      loginOrOut: "all",
      options: [
        {
          value: "all",
          label: "全部",
        },
        {
          value: "登录",
          label: "登录",
        },
        {
          value: "退出",
          label: "退出",
        },
      ],
      loginItems: [],
      logoutItems: [],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
 
    // 删除
    deleteHandle(id) {
      var ids = id
        ? [id]
        : this.dataListSelections.map((item) => {
            return item.id;
          });
      this.$confirm(
        `确定对[id=${ids.join(",")}]进行[${id ? "删除" : "批量删除"}]操作?`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      ).then(() => {
        this.$http
          .post(
            "/systemLog/delete",
            { ids },
            {
              headers: {
                token: this.token,
              },
            }
          )
          .then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "操作成功",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.getDataList();
                },
              });
            } else {
              this.$message.error(data.msg);
            }
          });
      });
    },
    //切换时间升序或者降序
    changeTimeOrder() {
      this.timeOrder = !this.timeOrder;
      this.getDataList();
    },
    //上传文件成功回调
    uploadSuccess(response, file, fileList) {
      console.log(response);
      if (response.code == 0) {
        //添加学生成功
        this.$message({
          message: "恭喜你，添加学生成功",
          type: "success",
        });
        this.getDataList();
      } else {
        this.$message.error("寄！添加学生失败");
      }
    },
    // 每页数
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 当前页
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 获取数据列表
    getDataList() {
      this.dataListLoading = true;
      this.$http
        .get("/systemLog/list", {
          params: {
            page: this.pageIndex,
            pageSize: this.pageSize,
            key: this.dataForm.key,
            timeOrder: this.timeOrder,
            loginOrOut: this.loginOrOut,
          },
          headers: {
            token: this.token,
          },
        })
        .then(({ data }) => {
          if (data && data.code === 0) {
            this.dataList = data.page.list;
            //分离出来登录/登出
            this.dataList.forEach((o) => {
              if (o.businessName == "登录") {
                this.loginItems.push(o);
              } else {
                this.logoutItems.push(o);
              }
            });
            //遍历处理身份
            this.dataList.forEach((o) => {
              if (o.role == "student") {
                o.role = "学生";
              }
              if (o.role == "teacher") {
                o.role = "老师";
              }
              if (o.role == "admin") {
                o.role = "管理员";
              }
            });
            this.totalPage = data.page.totalCount;
          } else {
            this.dataList = [];
            this.totalPage = 0;
          }
          this.dataListLoading = false;
        });
    },

    // 多选
    selectionChangeHandle(val) {
      this.dataListSelections = val;
      console.log(val);
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.token = this.$store.state.user.token;
    console.log(this.token);
    this.getDataList();
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style scoped>
.top {
  margin-bottom: 10px;
  display: flex;
}
.upload-demo {
  margin-left: 20px;
}
</style>